* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

#datetime-picker {
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 0 auto;
  padding: 10px;
  font-size: 16px;
  display: block;
}

button[data-start] {
  cursor: pointer;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 10px auto;
  padding: 5px 20px;
  font-size: 16px;
  display: block;
}

button[data-stop] {
  cursor: pointer;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 20px auto;
  padding: 5px 20px;
  font-size: 16px;
  display: block;
}

.timer {
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.timer .field {
  flex-direction: column;
  align-items: center;
  margin: 0 10px;
  display: flex;
}

.timer .field .value {
  font-size: 48px;
  font-weight: bold;
}

.timer .field .label {
  text-transform: uppercase;
  margin-top: 10px;
  font-size: 16px;
}

